import { Fragment } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import MainPage from "./MainPage/MainPage";
import PuzzlePage from "./PuzzlePage/PuzzlePage";

const App = () => {
  return (
    <Fragment>
      <Router>
        <Switch>
          <Route exact path="/">
            <MainPage />
          </Route>
          <Route exact path="/puzzle">
            <PuzzlePage />
          </Route>
        </Switch>
      </Router>
    </Fragment>
  );
}

export default App;
